              <h6 class="text-center col-12 mb-0">My Page [ ]</h6>
            </div>

            <div class="container-info-main" style="width: 100%">
              <div class="form-group row">
                <div class="bar"></div>
                <div class="value">
                  <div class="UserName" v-for="item in this.events" :key="item.email" style="font-size: 2vw">
