                  </div>
                </div>
                <div class="bar"></div>
                <div class="value">
                  <div class="UserName" v-for="item in this.events" :key="item.email">
                    {{ item.name }}
                  </div>
                </div>
              </div>

              <div class="form-group row align-items-center">
                <label for="targetCompany" class="bar"></label>
                <div class="value">
                  <div class="UserName">{{ this.events[0].mySex }}</div>
                </div>
                <label for="targetCompany" class="bar"></label>
                <div class="value">
