<script>
import { db } from '@/main';
import { mapState } from 'vuex';
import LoadingButton from './LoadingButton';

function displayMessage() {
  alert('  ');
  const html = document.querySelector('html');

  const panel = document.createElement('div');
  panel.setAttribute('class', 'msgBox');
  html.appendChild(panel);

  const msg = document.createElement('p');
  msg.textContent = 'This is a message box';
  panel.appendChild(msg);

  const closeBtn = document.createElement('button');
  closeBtn.textContent = 'x';
  panel.appendChild(closeBtn);

  closeBtn.onclick = function () {
    panel.parentNode.removeChild(panel);
  };
}
export default {
  components: {
    LoadingButton,
  },
  name: 'mypage',

  data: () => ({
    events: [],
    eventsCompany: [],
    eventsToeic: [],
    eventsToeicSpeaking: [],
    eventsOpic: [],
    eventsTexts: [],
    companyItems: [],
    eventNameCompany: 'KEPC',
    wantToGo: '',
    myToeicScore: '',
    myToeicSpeakingScore: '',
    myOpicScore: '',
    mySchoolScore: '',
    myCertification: '',
    myForeign: '',
    myTravel: '',
    myIntern: '',
    myContest: '',
    myAge: '',
    mySex: '',
    myEnglishTest: '',
    myStudyTime: '',
    mySoon: '',
    companyToeicScore: '',
    companyToeicSpeakingScore: '',
    companyOpicScore: '',
    companySchoolScore: '',
    companyCertification: '',
    companyForeign: '',
    companyTravel: '',
    companyIntern: '',
    companyContest: '',
    isLoading: false,
    dialog: false,
    dialogCheck: false,
    dialogToeic: false,
    dialogSchool: false,
    dialogCertification: false,
    dialogForeign: false,
    dialogToeicSpeaking: false,
    dialogOpic: false,
    dialogTravel: false,
    dialogIntern: false,
    dialogContest: false,
    dialogCal: false,
    currentlyEditing: null,
    selectedEvent: {},
    loading: false,
    randomNumber: 2,
    tempStudyTime: '',
    tempSoon: '',
    OpicState: { NL: 1, NM: 2, NH: 3, IL: 4, IM1: 5, IM2: 6, IM3: 7, AL: 8 },
    OpicType: '',
    OpicTypes: ['AL', 'IH', 'IM3', 'IM2', 'IM1', 'IL', 'NH', 'NM', 'NL'],
  }),
  computed: {
    ...mapState(['userInfo']),
    IsDate() {
      const today = this.getToday();
      const thatDay = this.getDates2(today, this.MathforOpicScore());
      console.log(thatDay);
      const thatsDay = parseInt(thatDay.substr(5, 2) * 30) + parseInt(thatDay.substr(8, 2));
      console.log(thatsDay);
      return thatsDay;
    },
    ToeicScore() {
      return Math.floor(
        (parseFloat(this.events[0].myToeicScore) / parseFloat(this.eventsCompany.companyToeicScore)) * 100
      ).toString();
    },
    ToeicPercentage() {
      return { width: (parseInt(this.ToeicScore) > 100 ? 100 : this.ToeicScore) + '%' };
    },
    ToeicProgressBar() {
      let temp = this.ToeicScore;
      if (parseInt(temp) < 40) {
        return 'progress-bar-danger';
      } else if (parseInt(temp) < 90) {
