        let appData = doc.data();
        // events 
        appData.id = doc.id;
        events.push(appData);
      });
      let a3 = events;
      a3.sort(function (a, b) {
        let a1 = a.end.substr(5, 2) + a.end.substr(8, 2);
        let b1 = b.end.substr(5, 2) + b.end.substr(8, 2);
